var Notification = /** @class */ (function () {
    function Notification() {
    }
    /**
     * Show success notification
     *
     * @param {Object} vueInstance - Vue instance
     * @param {String} message - Message which to display on Notification
     */
    Notification.success = function (vueInstance, message) {
        vueInstance.$notify({
            group: 'global',
            type: 'success',
            title: 'Success',
            text: message,
            duration: 3500
        });
    };
    /**
     * Show error notification
     *
     * @param {Object} vueInstance - Vue instance
     * @param {String} message - Message which to display on Notification
     */
    Notification.error = function (vueInstance, message) {
        vueInstance.$notify({
            group: 'global',
            type: 'error',
            title: 'Something went wrong',
            text: message,
            duration: 5000
        });
    };
    /**
     * Show warn notification
     *
     * @param {Object} vueInstance - Vue instance
     * @param {String} message - Message which to display on Notification
     */
    Notification.warning = function (vueInstance, message) {
        vueInstance.$notify({
            group: 'global',
            type: 'warn',
            title: 'Something went wrong',
            text: message,
            duration: 5000
        });
    };
    return Notification;
}());
export { Notification };
