import VueRouter from 'vue-router';
// Pages
import Products from '@/products/views/Products.vue';
var routePrefix = 'products';
var routes = [
    {
        path: '*',
        component: Products
    },
    {
        name: 'ticketWrapper',
        path: "" + routePrefix,
        component: Products
    }
];
export var router = new VueRouter({
    mode: 'history',
    routes: routes,
    linkActiveClass: 'is-active'
});
